import React from "react";
import { useState, useEffect } from "react";
import tr7TradingBot from "../Model/fetch-tr7-trading-bot.js";
import { CCard, CCardHeader, CCardBody, CRow, CCol } from "@coreui/react";

const TR7BotTrade = (props) => {
  const [message, $message] = useState(null);
  async function fetchSomeBotData() {
    const response = await tr7TradingBot.fetchTradeInfo();
    return response;
  }
  useEffect(() => {
    fetchSomeBotData().then((response) => $message(response));
    const id = setInterval(() => {
      fetchSomeBotData().then((response) => $message(response));
    }, 1500);

    return () => clearInterval(id);
  }, []);

  if (!message) {
    return <>Loading...</>;
  } else {
    console.log(message);
    const openOrders = message.openOrders;
    const positions = message.positions;
    return (
      <>
        <h1>Current Orders and Positions</h1>
        {openOrders.length > 0 && (
          <CRow>
            <CCol xl={12}>
              <CCard className="mb-4">
                <CCardHeader>Orders</CCardHeader>
                <CCardBody>
                  <p>All current orders</p>
                  <table className="table">
                    <thead>
                      <tr>
                        {Object.keys(openOrders[0]).map((key, index) => {
                          return <th index={index}>{key}</th>;
                        })}
                      </tr>
                    </thead>
                    <tbody>
                      {openOrders.map((order, index) => {
                        return (
                          <tr index={index}>
                            {Object.values(order).map((value, idx) => {
                              return (
                                <td>
                                  <span className="h6" index={idx}>
                                    {value}
                                  </span>
                                </td>
                              );
                            })}
                          </tr>
                        );
                      })}
                    </tbody>
                  </table>
                </CCardBody>
              </CCard>
            </CCol>
          </CRow>
        )}
        {positions.length > 0 && (
          <CRow>
            <CCol xl={12}>
              <CCard className="mb-4">
                <CCardHeader>Positions</CCardHeader>
                <CCardBody>
                  <p>All current positions</p>
                  <table className="table">
                    <thead>
                      <tr>
                        {Object.keys(positions[0]).map((key, index) => {
                          return <th index={index}>{key}</th>;
                        })}
                      </tr>
                    </thead>
                    <tbody>
                      {positions.map((position, index) => {
                        delete position.openOrderInitialMargin;
                        delete position.isolated;
                        delete position.maxNotional;
                        delete position.positionSide;
                        delete position.positionAmt;
                        delete position.notional;
                        delete position.isolatedWallet;
                        delete position.updateTime;
                        delete position.bidNotional;

                        return (
                          <tr index={index}>
                            {Object.values(position).map((value, idx) => {
                              return (
                                <td>
                                  <span className="h6" index={idx}>
                                    {value}
                                  </span>
                                </td>
                              );
                            })}
                          </tr>
                        );
                      })}
                    </tbody>
                  </table>
                </CCardBody>
              </CCard>
            </CCol>
          </CRow>
        )}
        {openOrders.length === 0 && positions.length === 0 && (
          <h1>There is not any open order or position</h1>
        )}
      </>
    );
  }
};

export default TR7BotTrade;
